#solo{
    height:100%;
    width:100vw;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-image: url("../../img/Fond.png");
    background-repeat:no-repeat;
    background-position-x:center;
    background-size: cover;
    overflow: hidden;
}

    #soloBlock{
        z-index:1;
        height:50%;
        width:80%;
        border-radius:5vw;
        background: rgba(0, 0, 0, 0.75);
        display:flex;
        justify-content:center;
        align-items:center;
        flex-direction: column;
    }

        #soloBox{
            height:70%;
            width:100%;
            display:flex;
            justify-content:space-around;
            align-items:center;
            flex-direction: column;
        }

            #soloTitle{
                margin:0;
                color:white;
                font-size:6.5vw;
                font-family: 'Sansita Swashed',cursive;
                font-weight: 700;
                padding-bottom:2%;
            }

            #soloImg{
                border-radius: 5vw;
            }
    
    #foundBox{
        height:50%;
        width:100%;
        display:flex;
        justify-content:center;
        align-items:center;
        flex-direction: column;
    }

        #foundSvg{
            height:50%;
            width:50%;
            stroke-dasharray: 300;
            stroke-dashoffset: 300;
            animation: drawFound 2s forwards;
        }

        @keyframes drawFound {
            0% {stroke-dashoffset: 300}
            100% {stroke-dashoffset: 0}
        }

    
        #foundTxt{
            font-size:7vw;
            margin:0;
            font-family: 'Sansita Swashed',cursive;
            color:white;
            text-align:center;
            font-weight: 700;
            animation: fadeIn 2s both;
        }